@import '../../styles/variables.scss';

.DesktopButton {
    width: 150px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    outline: none;
    cursor: pointer;
    color: $color-4;
    background-color: $color-3;
    border: 1px solid $color-4;
    letter-spacing: 1px;
    font-size: 14px;
}

.DesktopButton:hover {
    background-color: $color-1;
}

.MobileButton {
    width: 100px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    outline: none;
    cursor: pointer;
    color: $color-4;
    background-color: $color-3;
    border: 1px solid $color-4;
    letter-spacing: 1px;
    font-size: 12px;
}

.MobileButton:hover {
    background-color: $color-1;
}